}
.header-middle .header-searchbox .form-group .form-control:focus {
  color: #d8deec;
  background-color: #26293a;
}
.header-middle .header-searchbox .form-group > button {
  color: #a6afcc;
}
.header-right .dropdown .dropdown-toggle {
  color: #a6afcc;
}
/******************* MAIN MENU ******************/
.main-menu {
  background-color: #26293a;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
}
.main-menu.fixed {
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
}
.main-menu ul.nav > .nav-item > .nav-link {
  color: #a6afcc;
}
.main-menu ul.nav .sub-menu {
  background-color: #26293a;
  border-top: 1px solid #191b23;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
}
.main-menu ul.nav .sub-menu > ul > li {
  border-color: #191b23;
}
.main-menu ul.nav .sub-menu > ul > li a {
  color: #a6afcc;
}
.main-menu ul.nav .sub-menu ul + ul {
  border-color: #191b23;
}
.main-menu ul.nav .sub-item a::after {
  color: #828cad;
}
.main-menu.color-scheme-gray {
  box-shadow: none;
}
.main-menu.color-scheme-gray ul.nav > .nav-item > .nav-link:hover {
  color: #fff;
}
.main-menu.color-scheme-dark,
.main-menu.color-scheme-gray {
  background-image: linear-gradient(to left, rgba(38, 41, 58, 0.8) 30%, rgba(166, 175, 204, 0.1) 100%);
  background-color: #a6afcc;
}
.main-menu.color-scheme-dark ul.nav .sub-menu > ul > li a,
.main-menu.color-scheme-gray ul.nav .sub-menu > ul > li a {
  color: #828cad;
}
.main-menu.color-scheme-dark ul.nav .sub-menu > ul > li a:hover,
.main-menu.color-scheme-gray ul.nav .sub-menu > ul > li a:hover {
  color: #d8deec;
}
.main-menu.color-scheme-dark ul.nav .sub-menu > ul > li.active a,
.main-menu.color-scheme-gray ul.nav .sub-menu > ul > li.active a {
  color: #d8deec;
}
.main-menu.color-scheme-dark ul.nav > .nav-item.active > .nav-link,
.main-menu.color-scheme-gray ul.nav > .nav-item.active > .nav-link {
  color: #fff;
}
